import {Component, Input, OnInit} from '@angular/core';
import {ServiceRequests} from '../dashboard';
import {DashboardService} from '../dashboard.service';

@Component({
    selector: 'app-services-requests',
    templateUrl: './services-requests.component.html',
    styleUrls: ['./services-requests.component.scss']
})
export class ServicesRequestsComponent implements OnInit {
    constructor(
        private dash: DashboardService
    ) {
    }

    @Input() duration: string;

    max = 200;
    loading = true;
    total = 0;
    data: ServiceRequests;

    ngOnInit() {
        this.dash.getServicesRequests().then(res => {
            this.data = res.data;
            this.total = this.data.reduce((prev, curr, i) => {
                return prev + curr.number_of_calls_made;
            }, 0);
            this.loading = false;
        });
    }
}
