import {Component, OnInit} from '@angular/core';
import {ApexOptions} from 'ng-apexcharts';
import {YearData} from '../dashboard';
import {DashboardService} from '../dashboard.service';
import {ToastrService} from 'ngx-toastr';

@Component({
    selector: 'app-dashboard',
    templateUrl: './dashboard.component.html',
    styleUrls: ['./dashboard.component.scss']
})
export class DashboardComponent implements OnInit {
    duration = '3M';
    show = true;
    loading = {
        line: true,
        donut: true,
        bar: true,
    };
    durations = [
        {label: 'Week', key: 'W'},
        {label: '3 Months', key: '3M'},
        {label: 'Month', key: 'M'},
        {label: 'Day', key: 'D'},
        {label: 'Hour', key: 'H'},
        {label: 'Year', key: 'Y'}
    ];
    yearData: YearData;
    totalSpent:number
    yearDataGlobal: { [month: string]: number };
    public chartOptions: Partial<ApexOptions>;
    public lineChart: Partial<ApexOptions>;
    public donut: Partial<ApexOptions>;

    constructor(
        private dash: DashboardService,
        private toastr: ToastrService
    ) {
        this.duration = dash.getDuration();
    }

    setDuration() {
        this.dash.setDuration(this.duration);
        this.show = false;
        setTimeout(() => this.show = true, 20);
        this.init()
    }

    makeLineChart() {
        this.loading.line = true;
        this.dash.getYearlyRequestsGlobal().then(res => {
            this.yearDataGlobal = res.data;
            this.lineChart = {
                yaxis: {
                    labels: {
                        formatter: function (value) {
                            let val: any = Math.abs(value);
                            if (val >= 10 ** 3 && val < 10 ** 6) {
                                val = (val / 1000).toFixed(0) + ' K';
                            } else if (val >= 10 ** 6) {
                                val = (val / 1000000).toFixed(0) + ' M';
                            }
                            return val;
                        }
                    }
                },
                colors: ['#30345e'],
                series: [
                    {
                        name: 'Requests',
                        data: Object.values(this.yearDataGlobal)
                    }
                ],
                chart: {
                    toolbar: {
                        show: false
                    },
                    height: 350,
                    type: 'line',
                    zoom: {
                        enabled: false
                    }
                },
                dataLabels: {
                    enabled: false
                },
                stroke: {
                    curve: 'straight'
                },
                grid: {
                    borderColor: 'transparent'
                    // column: {
                    //   colors: ["#f3f3f3", "transparent"], // takes an array which will be repeated on columns
                    //   opacity: 0.5
                    // }
                },
                xaxis: {
                    categories: Object.keys(this.yearDataGlobal)
                }
            };
            this.loading.line = false;
        }).catch(err => {
            console.error('err line: ', err);
            this.toastr.error('something went wrong with Line chart');
        });
    }

    makeChart() {
        this.loading.bar = true;
        this.dash.getYearlyRequests().then(res => {
            console.log(res);
            this.yearData = res.data;
            this.totalSpent=res.total_spent
            this.chartOptions = {
                series: this.makeSeries(),
                dataLabels: {
                    enabled: false
                },
                colors: ['#ff4c51', '#ff9f43', '#ffb619'],
                chart: {
                    type: 'bar',
                    height: 350,
                    stacked: true,
                    toolbar: {
                        show: false
                    },
                    zoom: {
                        enabled: true
                    }
                },
                responsive: [
                    {
                        breakpoint: 480,
                        options: {
                            legend: {
                                position: 'bottom',
                                offsetX: -10,
                                offsetY: 0
                            }
                        }
                    }
                ] as any,
                plotOptions: {
                    bar: {
                        borderRadius: 7,
                        horizontal: false
                    }
                } as any,
                xaxis: {
                    type: 'category',
                    categories: Object.keys(this.yearData)
                },
                legend: {
                    position: 'bottom',
                } as any,
                fill: {
                    opacity: 1
                }
            };
            this.makeDonut()
            this.loading.bar = false;
        }).catch(err => {
            this.toastr.error('Something went wrong with the monthly requests chart.');
        });
    }
    makeDonut(){
        if(!this.yearData){
            return
        }
        let services= [].concat(...Object.values(this.yearData))
        console.log('services : ',services)
        // for(let month)
        this.donut = {
            plotOptions: {
                pie: {
                    donut: {
                        labels: {
                            show: true,
                            total: {
                                show: true,
                                showAlways: true,
                                label: 'Total Spent',
                                formatter: () => ''+this.totalSpent }
                        }
                    }
                }
            },
            series: services.map(s=>Number(Number(s.total_spent).toPrecision(2))),
            chart: {
                type: 'donut'
            },
            legend: {
                show: false
            },
            dataLabels: {
                enabled: false
            },
            labels: services.map(s=>s.service_name),
        };
        this.loading.donut = false;
    }

    makeSeries() {
        let data = this.yearData;
        const services = {};
        for (const month in data) {
            data[month].forEach(item => {
                services[item.service_name] = true;
            });
        }
        const series = Object.keys(services).map(service => ({
            name: service,
            data: []
        }));
        for (const month in data) {
            series.forEach(seriesItem => {
                const serviceData = data[month].find(item => item.service_name === seriesItem.name);
                seriesItem.data.push(serviceData ? serviceData.number_of_calls_made : 0);
            });
        }
        return series;
    }

    init() {
        this.makeLineChart();
        this.makeChart();
    }

    ngOnInit() {
        this.init();
    }


}
