import {Component, Input, OnInit} from '@angular/core';
import {DashboardService} from '../dashboard.service';
import {TokenRequests} from '../dashboard';

@Component({
    selector: 'app-token-requests',
    templateUrl: './token-requests.component.html',
    styleUrls: ['./token-requests.component.scss']
})
export class TokenRequestsComponent implements OnInit {
    constructor(
        private dash: DashboardService
    ) {
    }

    @Input() duration: string;
    max = 200;
    total = 0;
    loading = true;
    data: TokenRequests;

    ngOnInit() {
        this.dash.getTokenRequests().then(res => {
            this.data = res.data;
            this.total = this.data.reduce((prev, curr, i) => {
                return prev + curr.number_of_calls_made;
            }, 0);
            this.loading = false;
        });
    }
}






