import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { DashboardComponent } from './dashboard/dashboard.component';
import {RouterModule, Routes} from "@angular/router";
import {NgSelectModule} from "@ng-select/ng-select";
import {FormsModule} from "@angular/forms";
import {NgApexchartsModule} from "ng-apexcharts";
import {NgbProgressbarModule, NgbTooltipModule} from '@ng-bootstrap/ng-bootstrap';
import { ServicesRequestsComponent } from './services-requests/services-requests.component';
import { TokenRequestsComponent } from './token-requests/token-requests.component';
import {ComponentsModule} from '../../components/components.module';


const routes: Routes = [{
  path: '',
  component: DashboardComponent
}]

@NgModule({
  declarations: [
    DashboardComponent,
    ServicesRequestsComponent,
    TokenRequestsComponent
  ],
    imports: [
        CommonModule,
        NgSelectModule,
        NgApexchartsModule,
        NgbProgressbarModule,
        FormsModule,
        RouterModule.forChild(routes),
        ComponentsModule,
        NgbTooltipModule,
    ]
})
export class DashboardModule { }
