import {Injectable} from '@angular/core';
import {HttpClientService} from '../../../../@core/services/http-client.service';
import {Constants} from '../../../constants';
import moment from 'moment';

@Injectable({
    providedIn: 'root'
})
export class DashboardService {

    constructor(
        private http: HttpClientService
    ) {
    }

    show = true;

    private duration = '3M';

    setDuration(d: string) {
        this.duration = d;
    }

    getDuration() {
        return this.duration;
    }

    getServicesRequests() {
        return this.http.getData(Constants.API_SERVICES_REQUESTS, this.getPeriod());
    }

    getTokenRequests() {
        return this.http.getData(Constants.API_TOKEN_REQUESTS, this.getPeriod());
    }

    getYearlyRequests() {
        return this.http.getData(Constants.API_YEARLY_REQUESTS);
    }

    getYearlyRequestsGlobal() {
        return this.http.getData(Constants.API_YEARLY_REQUESTS_GLOBAL);
    }

    getPeriod() {
        let p = {};
        switch (this.duration) {
            case 'W':
                p = {
                    start_date: moment().startOf('week').format('YYYY-MM-DD'),
                    end_date: moment().endOf('week').format('YYYY-MM-DD')
                };
                break;
            case '3M':
                p = {
                    start_date: moment().subtract(3, 'months').startOf('month').format('YYYY-MM-DD'),
                    end_date: moment().endOf('month').format('YYYY-MM-DD')
                };
                break;
            case 'M':
                p = {
                    start_date: moment().startOf('month').format('YYYY-MM-DD'),
                    end_date: moment().endOf('month').format('YYYY-MM-DD')
                };
                break;
            case 'D':
                p = {
                    start_date: moment().startOf('day').format('YYYY-MM-DD'),
                    end_date: moment().endOf('day').format('YYYY-MM-DD')
                };
                break;
            case 'H':
                p = {
                    start_date: moment().startOf('hour').format('YYYY-MM-DD'),
                    end_date: moment().endOf('hour').format('YYYY-MM-DD')
                };
                break;
            case 'Y':
                p = {
                    start_date: moment().startOf('year').format('YYYY-MM-DD'),
                    end_date: moment().endOf('year').format('YYYY-MM-DD')
                };
                break;
            // default:
            //     p = {
            //         start_date: moment().subtract(3, 'months').startOf('month').format('YYYY-MM-DD'),
            //         end_date: moment().endOf('month').format('YYYY-MM-DD')
            //     };
            //     break;
        }
        return p;
    }
}
